* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Jost', sans-serif;
  }
  body {
      background: #f3f2ef;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
      height: 100vh;
      width: 100vh;
  }
  html {
      font-size: 100%;
      height: 100%;
  }
  html {
      background: #eee;
  }
  .box {
      width: 25vw;
      height: 60vh;
      border-radius: 0.5rem;
      box-shadow: 0 0.2rem 3rem rgba(0,0,0,0.2);
      background: #a5bbdd;
      position: relative;
      overflow: hidden;
      transform: translate3d(0,0,0);
      min-width: 20rem;
      min-height: 35rem;
  }
  .wave {
      opacity: 0.3;
      position: absolute;
      top: 170%;
      left: 50%;
      background: red;
      width: 50rem;
      height: 50rem;
      margin-left: -25rem;
      margin-top: -20rem;
      transform-origin: 50% 48%;
      border-radius: 43%;
      animation: drift 3000ms infinite linear;
      z-index: 1;
  }
  .wave .-three {
      animation: drift 5000ms infinite linear;
      opacity: 0.2;
      z-index: 2 !important;
  }
  .wave .-two {
    animation: drift 7000ms infinite linear;
    opacity: 0.1;
    z-index: 3 !important;
  }
  .box::after {
      content: "";
      display: block;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      z-index: 11;
      transform: translate3d(0,0,0);
  }
  @keyframes drift {
      from {
          transform: rotate(0deg);
      }
      from {
          transform: rotate(360deg);
      }
  }